import React from 'react';
import { HashRouter as Router, Route, Routes, Link } from 'react-router-dom';

const App = () => {
  // Determine if we're running on GitHub Pages
  const isGitHubPages = window.location.hostname.includes('github.io');
  
  return (
    <Router basename={isGitHubPages ? "/uwa-pile-capacity-calculator" : "/"}>
      <div>
        <h1>UWA Pile Capacity Calculator</h1>
        <nav>
          <Link to="/">Home</Link>
          <Link to="/driven">Driven Piles</Link>
          <Link to="/bored">Bored Piles</Link>
          <Link to="/screw">Screw Piles</Link>
        </nav>
        <Routes>
          <Route path="/" element={<h2>Welcome to the UWA Pile Capacity Calculator</h2>} />
          <Route path="/driven" element={<h2>Driven Piles</h2>} />
          <Route path="/bored" element={<h2>Bored Piles</h2>} />
          <Route path="/screw" element={<h2>Screw Piles</h2>} />
        </Routes>
      </div>
    </Router>
  );
};

export default App;